.kingNav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin: 16px;
}

.kingButton {
  border: none;
  border-radius: 8px;
  padding: 8px;
  margin: 0;
  background: #496989;
  color: white;
  font-size: 1rem;

  &:hover {
    background: #191919;
    cursor: pointer;
    transform: translateY(-2px);
    transition: 0.4s;
  }

  &[disabled] {
    background: grey;
    cursor: revert;
    transform: revert;
  }

  &[aria-current] {
    background: #c51605;
    font-weight: bold;
    cursor: revert;
    transform: revert;
  }
}
